/* Reset default styles */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif; /* Modern and elegant font */
}

/* Content Styling */
.content {
  min-height: auto;
  width: 100%;
  padding: 25px;
  background: #f7f8fb; /* Light, calming background */
  border-radius: 10px;
  transition: background-color 0.3s ease;
}

/* Navbar Styling */
.navbar {
  background: linear-gradient(135deg, #007bff, #3a8bff); /* Soft gradient for a more polished look */
  padding: 20px 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: white;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
  border-radius: 8px;
  transition: all 0.3s ease;
}

/* Navbar Title */
.navbar-title {
  font-size: 28px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: white;
  text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.1);
}

/* Navbar Hover Effect */
.navbar:hover {
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
}

/* Register Page */
.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
  width: 100%;
  background: linear-gradient(135deg, #f7f7f7, #e3e3e3); /* Subtle gradient */
  border-radius: 15px;
  box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.1);
  animation: fadeIn 1s ease-in-out;
}

/* Add background styling for the login page */
.login-page {
  background-image: url('./assets/finance-definition.jpg');
  background-size: cover;
  background-position: center;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 20px;
}

/* Styling for the heading box */
.heading-box {
  background-color: rgba(0, 0, 0, 0.7); /* Darker semi-transparent background */
  padding: 20px 30px;
  border-radius: 10px;
  text-align: center;
  margin-bottom: 20px; /* Space between heading and form */
}

.heading-box h1 {
  color: #fff;
  font-size: 2.5rem;
  margin: 0;
  font-weight: 700;
}

/* Toast Notification Fix (Ant Design) */
.ant-message {
  z-index: 9999;
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
  animation: fadeIn 1s ease-in-out;
}

/* Filters Section */
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 25px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.1);
  background-color: white;
  border-radius: 12px;
  margin: 25px;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* Filters Hover Effect */
.filters:hover {
  transform: scale(1.02);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.2);
}

/* Footer Styling */
.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: linear-gradient(135deg, #111, #333); /* Dark gradient with style */
  color: white;
  text-align: center;
  padding: 15px;
  font-size: 14px;
  box-shadow: 0 -2px 8px rgba(0, 0, 0, 0.2);
  border-radius: 12px 12px 0 0;
  transition: all 0.3s ease;
}

/* Table Styling */
.ant-table {
  background-color: #f9f9f9 !important; /* Slight off-white background */
  border-radius: 12px;
  margin: 25px;
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

/* Table Header */
.ant-table-thead > tr > th {
  background-color: #e6e6e6 !important;
  color: #333 !important;
  text-align: center;
  font-weight: bold;
  padding: 14px;
  font-size: 17px;
  border-bottom: 2px solid #bbb;
  border-radius: 6px 6px 0 0;
}

/* Table Rows */
.ant-table-tbody > tr {
  transition: background 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.ant-table-tbody > tr:hover {
  background-color: #f1f1f1 !important; /* Slight hover effect */
  transform: translateY(-5px);
}

.ant-table-tbody > tr > td {
  background-color: #fafafa !important;
  padding: 12px;
  text-align: center;
  font-size: 15px;
  border-bottom: 1px solid #ddd;
  border-radius: 0 0 6px 6px;
}

/* Buttons */
.btn {
  padding: 12px 18px;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 15px;
  font-weight: 600;
  transition: all 0.3s ease-in-out;
  box-shadow: 0px 3px 8px rgba(0, 0, 0, 0.2);
}

/* Primary Button */
.btn-primary {
  background: linear-gradient(135deg, #00b5e2, #005f85); /* Vibrant gradient */
  color: white;
}

.btn-primary:hover {
  background: linear-gradient(135deg, #005f85, #003d5f);
  transform: scale(1.05);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.2);
}

/* Hover and Focus Effects for Input and Buttons */
input:focus, .btn:focus {
  outline: none;
  box-shadow: 0 0 8px rgba(0, 191, 255, 0.6);
  border: 2px solid #00b5e2;
}

/* Responsive Fixes */
@media (max-width: 768px) {
  .filters {
    flex-direction: column;
    align-items: flex-start;
  }

  .content {
    padding: 20px;
  }

  .footer {
    font-size: 12px;
    padding: 10px;
  }

  .btn {
    width: 100%;
    text-align: center;
  }

  .ant-table {
    overflow-x: auto;
  }

  .navbar-title {
    font-size: 22px;
  }
}

/* Keyframes for fade-in effect */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

/* Transaction Page */

/* AntD Icons */
.switch-icons {
  border: 1px solid rgba(0, 0, 0, 0.683);
  border-radius: 5px;
  padding: 10px 15px;
}

.anticon svg {
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: black;
}

.inactive-icon {
  color: gray;
}
